import "./Create.css";
import { createPost } from "../../services/data";
import validate from "../../services/validations";

const Create = ({ history }) => {
  const onSubmitHandler = async (e) => {
    e.preventDefault();

    const formData = new FormData(e.target);
    const title = formData.get("title");
    const description = formData.get("description");
    const image = formData.get("image");
    const location = formData.get("location");
    const difficulty = formData.get("difficulty");
    const category = formData.get("category");
    const moreInfo = formData.get("extra-info");
    const username = sessionStorage.getItem("username");
    const likes = [];

    const data = {
      title,
      description,
      image,
      location,
      difficulty,
      category,
      moreInfo,
      username,
      likes,
    };

    if (validate(data)) {
      await createPost(data);
      history.push("/catalogue");
    }
  };

  return (
    <div className="create-form-wrapper">
      <form onSubmit={onSubmitHandler} className="create-form">
        <div>
          <label htmlFor="title">–ó–∞–≥–ª–∞–≤–∏–µ:</label>
          <input id="title" type="text" placeholder="–∑–∞–≥–ª–∞–≤–∏–µ" name="title" />
        </div>
        <label htmlFor="description">–û–ø–∏—Å–∞–Ω–∏–µ:</label>
        <div>
          <textarea name="description" id="description" rows="7" placeholder="–æ–ø–∏—Å–∞–Ω–∏–µ"></textarea>
        </div>
        <div>
          <label htmlFor="image">–°–Ω–∏–º–∫–∞:</label>
          <input id="image" type="text" placeholder="image url" name="image" />
        </div>
        <div>
          <label htmlFor="location">–ú–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ:</label>
          <input id="location" type="text" placeholder="–ª–æ–∫–∞—Ü–∏—è" name="location" />
        </div>
        <div>
          <label htmlFor="difficulty">–¢—Ä—É–¥–Ω–æ—Å—Ç:</label>
          <select id="difficulty" name="difficulty">
            <option value="–ù–∏—Å–∫–∞">–ù–∏—Å–∫–∞</option>
            <option value="–°—Ä–µ–¥–Ω–∞">–°—Ä–µ–¥–Ω–∞</option>
            <option value="–í–∏—Å–æ–∫–∞">–í–∏—Å–æ–∫–∞</option>
            <option value="–ú–∞—à–∏–Ω–∞">–ú–∞—à–∏–Ω–∞</option>
          </select>
        </div>
        <div>
          <label htmlFor="category">–ö–∞—Ç–µ–≥–æ—Ä–∏—è:</label>
          <select id="category" name="category">
            <option value="eco">–ï–∫–æ–ø—ä—Ç–µ–∫–∞</option>
            <option value="peak">–í—Ä—ä—Ö</option>
            <option value="hut">–•–∏–∂–∞</option>
            <option value="camp">–ö—ä–º–ø–∏–Ω–≥</option>
          </select>
        </div>
        <label htmlFor="extra-info">–î–æ–ø—ä–ª–Ω–∏—Ç–µ–ª–Ω–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è:</label>
        <div>
          <textarea name="extra-info" id="extra-info" rows="7" placeholder="–æ—â–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è"></textarea>
        </div>
        <button className="createBtn">–°—ä–∑–¥–∞–π</button>
      </form>
    </div>
  );
};

export default Create;
