import { useContext } from "react";
import { Link } from "react-router-dom";

import "./Login.css";
import TokenContext from "../../../src/context/TokenContext";
import { login } from "../../services/data";
import notify from "../../services/notify";

const Login = ({ history }) => {
  const setToken = useContext(TokenContext);
  const onSubmitHandler = async (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const username = formData.get("username");
    const password = formData.get("password");

    if (username === "" || password === "") {
      return notify("–í—Å–∏—á–∫–∏ –ø–æ–ª–µ—Ç–∞ —Å–∞ –∑–∞–¥—ä–ª–∂–∏—Ç–µ–ª–Ω–∏.");
    }

    await login(username, password);
    setToken(sessionStorage.getItem("authToken"));
    history.push("/");
  };

  return (
    <div className="login-form-wrapper">
      <form onSubmit={onSubmitHandler} className="login-form">
        <div>
          <i className="fas fa-user"></i>
          <input id="loginUsername" type="text" placeholder="–ø–æ—Ç—Ä–µ–±–∏—Ç–µ–ª—Å–∫–æ –∏–º–µ" name="username" />
        </div>
        <div>
          <i className="fas fa-key"></i>
          <input id="loginPassword" type="password" placeholder="–ø–∞—Ä–æ–ª–∞" name="password" />
        </div>
        <button>–í–•–û–î</button>
        <p className="message">
          –ù—è–º–∞—Ç–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è?
          <Link to="/register">–°—ä–∑–¥–∞–π –ø—Ä–æ—Ñ–∏–ª</Link>
        </p>
      </form>
    </div>
  );
};

export default Login;
