import "./Categories.css";
import CategoryCard from "./CategoryCard";

const Categories = () => {
  const categoryCardData = {
    eco: `–ï–∫–æ–ø—ä—Ç–µ–∫–∏—Ç–µ —Å–∞ –∏–Ω—Ç–µ—Ä–∞–∫—Ç–∏–≤–Ω–∏ —Ç—É—Ä–∏—Å—Ç–∏—á–µ—Å–∫–∏ –º–∞—Ä—à—Ä—É—Ç–∏.–¢–µ –æ–±–∏–∫–Ω–æ–≤–µ–Ω–æ —Å–∞ –∫—Ä–∞—Ç–∫–∏ ‚Äì –ø—Ä–µ–º–∏–Ω–∞–≤–∞—Ç —Å–µ –∑–∞ –æ–∫–æ–ª–æ 2 ‚Äì 3 —á–∞—Å–∞ –∏
    —á–µ—Å—Ç–æ —Å–∞ –∫—Ä—ä–≥–æ–≤–∏. –ó–∞–¥—ä–ª–∂–∏—Ç–µ–ª–Ω–æ –∏–∑–∏—Å–∫–≤–∞–Ω–µ —Å–∞ —Ç–∞–±–µ–ª–∏—Ç–µ —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –∑–∞ –≤—Å–∏—á–∫–æ –∏–Ω—Ç–µ—Ä–µ—Å–Ω–æ, –∫–æ–µ—Ç–æ –º–æ–∂–µ –¥–∞ —Å–µ –≤–∏–¥–∏ –Ω–∞–æ–∫–æ–ª–æ ‚Äì –ø–µ–π–∑–∞–∂–∏,
    —Ä–∞—Å—Ç–µ–Ω–∏—è, –∂–∏–≤–æ—Ç–Ω–∏ –∏ –ø—Ä–∏—Ä–æ–¥–Ω–∏ –∑–∞–±–µ–ª–µ–∂–∏—Ç–µ–ª–Ω–æ—Å—Ç–∏.`,
    peak: `–ë—ä–ª–≥–∞—Ä–∏—è —Ä–∞–∑–ø–æ–ª–∞–≥–∞ —Å –≥–æ–ª—è–º–æ —Ä–∞–∑–Ω–æ–æ–±—Ä–∞–∑–∏–µ –æ—Ç –≤—ä—Ä—Ö–æ–≤–µ. –ù—è–∫–æ–π –æ—Ç —Ç—è—Ö –Ω–µ –∏–∑–∏—Å–∫–≤–∞—Ç –æ—Å–æ–±–µ–Ω–æ –≥–æ–ª—è–º–∞ –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∞ –∏ –º–æ–≥–∞—Ç –¥–∞ –±—ä–¥–∞—Ç
    –∏–∑–∫–∞—á–µ–Ω–∏ –æ—Ç –º–∞–ª–∫–∏ –∏ –≥–æ–ª–µ–º–∏, –¥—Ä—É–≥–∏ –æ–±–∞—á–µ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è–≤–∞—Ç –ø–æ-–≥–æ–ª—è–º–æ –ø—Ä–µ–¥–∏–∑–≤–∏–∫–∞—Ç–µ–ª—Å—Ç–≤–æ –¥–∞ –±—ä–¥–∞—Ç –ø–æ–∫–æ—Ä–µ–Ω–∏. –í –∫–∞—Ç–∞–ª–æ–≥–∞ —Å –≤—ä—Ä—Ö–æ–≤–µ –º–æ–∂–µ—Ç–µ
    –¥–∞ –æ—Ç–∫—Ä–∏–µ—Ç–µ –ø–æ–¥—Ö–æ–¥—è—â–∏—è –∑–∞ –≤–∞—Å –≤—Ä—ä—Ö.`,
    hut: `–ü–æ –ø–ª–∞–Ω–∏–Ω–∏—Ç–µ –≤ –ë—ä–ª–≥–∞—Ä–∏—è —Å–∞ –∏–∑–≥—Ä–∞–¥–µ–Ω–∏ –º–Ω–æ–≥–æ —Ö–∏–∂–∏, –∫–æ–∏—Ç–æ –æ—Å–∏–≥—É—Ä—è–≤–∞—Ç –Ω–∞ —Ç—É—Ä–∏—Å—Ç–∏—Ç–µ —É—Å–ª–æ–≤–∏—è –∑–∞ –æ—Ç–¥–∏—Ö –∏ –Ω–æ—â—É–≤–∫–∞.–ß–µ—Å—Ç–æ —Ö–∏–∂–∏ –≤
    –æ—Ç–¥–∞–ª–µ—á–µ–Ω–∏ —Ä–∞–π–æ–Ω–∏ –Ω—è–º–∞—Ç –ø–µ—Ä—Å–æ–Ω–∞–ª, –≤—ä–ø—Ä–µ–∫–∏ —á–µ –Ω–∞ –ø–ª–∞–Ω–∏–Ω–∞—Ä–∏ –µ —Ä–∞–∑—Ä–µ—à–µ–Ω –¥–æ—Å—Ç—ä–ø –¥–æ —Ç—è—Ö. –¢—É–∫ —â–µ –æ—Ç–∫—Ä–∏–µ—Ç–µ –±–æ–≥–∞—Ç –∏–∑–±–æ—Ä –æ—Ç —Ö–∏–∂–∏, –≤ –∫–æ–∏—Ç–æ
    –º–æ–∂–µ—Ç–µ –¥–∞ –ø—Ä–µ–Ω–æ—â—É–≤–∞—Ç–µ —Å–ª–µ–¥ –¥—ä–ª—ä–≥ –∏ —É–º–æ—Ä–∏—Ç–µ–ª–µ–Ω –ø—Ä–µ—Ö–æ–¥.`,
    camp: `–ö—ä–º–ø–∏–Ω–≥—ä—Ç –µ —Ñ–æ—Ä–º–∞ –Ω–∞ —Ç—É—Ä–∏–∑—ä–º, –ø—Ä–∏ –∫–æ—è—Ç–æ —Å–µ –Ω–æ—â—É–≤–∞ —Å—Ä–µ–¥ –ø—Ä–∏—Ä–æ–¥–∞—Ç–∞, –≤ –ø–∞–ª–∞—Ç–∫–∏ –∏–ª–∏ –∫–∞—Ä–∞–≤–∞–Ω–∏. –ü—Ä–∏–º–∏—Ç–∏–≤–Ω–∏—Ç–µ –∫—ä–º–ø–∏–Ω–≥–∏ –º–æ–∂–µ –¥–∞ –Ω–µ
    –ø—Ä–µ–¥–ª–∞–≥–∞—Ç –Ω–∏–∫–∞–∫–≤–∏ —É–¥–æ–±—Å—Ç–≤–∞, –∞ —Å–∞–º–æ –º—è—Å—Ç–æ –∑–∞ –ø–∞–ª–∞—Ç–∫–∞. –í –¥–æ–±—Ä–µ –æ–±–æ—Ä—É–¥–≤–∞–Ω–∏—Ç–µ –∫—ä–º–ø–∏–Ω–≥–∏ –∏–º–∞ –¥—É—à–æ–≤–µ, –ø—Ä–µ–∫–∞—Ä–∞–Ω–æ –µ–ª–µ–∫—Ç—Ä–∏—á–µ—Å—Ç–≤–æ, –ø–∞—Ä–∫–∏–Ω–≥,
    —Ç–æ–∞–ª–µ—Ç–Ω–∏ –∏ –¥—Ä—É–≥–∏.`,
  };
  return (
    <div className="categories">
      <CategoryCard title="–ö—ä–º –µ–∫–æ–ø—ä—Ç–µ–∫–∏" content={categoryCardData.eco} />
      <CategoryCard title="–ö—ä–º –≤—ä—Ä—Ö–æ–≤–µ" content={categoryCardData.peak} />
      <CategoryCard title="–ö—ä–º —Ö–∏–∂–∏" content={categoryCardData.hut} />
      <CategoryCard title="–ö—ä–º –∫—ä–º–ø–∏–Ω–≥–∏" content={categoryCardData.camp} />
    </div>
  );
};

export default Categories;
