import { useEffect, useState } from "react";

import "./EditPost.css";
import validate from "../../services/validations";
import { getPostById, editPost } from "../../services/data";

const EditPost = ({ history, match }) => {
  const [post, setPost] = useState({});

  useEffect(() => {
    async function fetchData() {
      const results = await getPostById(match.params.id);
      setPost(results[0]);
    }
    fetchData();
  }, [post]);

  const onSubmitHandler = async (e) => {
    e.preventDefault();

    const formData = new FormData(e.target);
    const title = formData.get("title");
    const description = formData.get("description");
    const image = formData.get("image");
    const location = formData.get("location");
    const difficulty = formData.get("difficulty");
    const category = formData.get("category");
    const moreInfo = formData.get("extra-info");
    const username = sessionStorage.getItem("username");
    const likes = [];

    const data = {
      title,
      description,
      image,
      location,
      difficulty,
      category,
      moreInfo,
      username,
      likes,
    };

    if (validate(data)) {
      await editPost(match.params.id, data);
      history.push(`/details/${match.params.id}`);
    }
  };

  return (
    <div className="edit-form-wrapper">
      <form onSubmit={onSubmitHandler} className="edit-form">
        <div>
          <label htmlFor="title">–ó–∞–≥–ª–∞–≤–∏–µ:</label>
          <input id="title" type="text" placeholder="–∑–∞–≥–ª–∞–≤–∏–µ" defaultValue={post?.title} name="title" />
        </div>
        <label htmlFor="description">–û–ø–∏—Å–∞–Ω–∏–µ:</label>
        <div>
          <textarea name="description" id="description" rows="7" placeholder="–æ–ø–∏—Å–∞–Ω–∏–µ" defaultValue={post?.description}></textarea>
        </div>
        <div>
          <label htmlFor="image">–°–Ω–∏–º–∫–∞:</label>
          <input id="image" type="text" placeholder="image url" defaultValue={post?.image} name="image" />
        </div>
        <div>
          <label htmlFor="location">–ú–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ:</label>
          <input id="location" type="text" placeholder="–ª–æ–∫–∞—Ü–∏—è" defaultValue={post?.location} name="location" />
        </div>
        <div>
          <label htmlFor="difficulty">–¢—Ä—É–¥–Ω–æ—Å—Ç:</label>
          <select id="difficulty" name="difficulty">
            <option selected={post?.difficulty === "–ù–∏—Å–∫–∞"} value="–ù–∏—Å–∫–∞">
              –ù–∏—Å–∫–∞
            </option>
            <option selected={post?.difficulty === "–°—Ä–µ–¥–Ω–∞"} value="–°—Ä–µ–¥–Ω–∞">
              –°—Ä–µ–¥–Ω–∞
            </option>
            <option selected={post?.difficulty === "–í–∏—Å–æ–∫–∞"} value="–í–∏—Å–æ–∫–∞">
              –í–∏—Å–æ–∫–∞
            </option>
            <option selected={post?.difficulty === "–ú–∞—à–∏–Ω–∞"} value="–ú–∞—à–∏–Ω–∞">
              –ú–∞—à–∏–Ω–∞
            </option>
          </select>
        </div>
        <div>
          <label htmlFor="category">–ö–∞—Ç–µ–≥–æ—Ä–∏—è:</label>
          <select id="category" name="category">
            <option selected={post?.category === "eco"} value="eco">
              –ï–∫–æ–ø—ä—Ç–µ–∫–∞
            </option>
            <option selected={post?.category === "peak"} value="peak">
              –í—Ä—ä—Ö
            </option>
            <option selected={post?.category === "hut"} value="hut">
              –•–∏–∂–∞
            </option>
            <option selected={post?.category === "camp"} value="camp">
              –ö—ä–º–ø–∏–Ω–≥
            </option>
          </select>
        </div>
        <label htmlFor="extra-info">–î–æ–ø—ä–ª–Ω–∏—Ç–µ–ª–Ω–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è:</label>
        <div>
          <textarea name="extra-info" id="extra-info" rows="7" placeholder="–æ—â–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è" defaultValue={post?.moreInfo}></textarea>
        </div>
        <button className="editBtn">–†–µ–¥–∞–∫—Ç–∏—Ä–∞–π</button>
      </form>
    </div>
  );
};

export default EditPost;
