import { useContext } from "react";
import { Link } from "react-router-dom";

import "./Register.css";
import TokenContext from "../../../src/context/TokenContext";
import { register } from "../../services/data";
import notify from "../../services/notify";

const Register = ({ history }) => {
  const setToken = useContext(TokenContext);

  const onSubmitHandler = async (e) => {
    e.preventDefault();

    const formData = new FormData(e.target);
    const username = formData.get("username");
    const password = formData.get("password");
    const rePass = formData.get("repass");

    if (username === "" || password === "" || rePass === "") {
      return notify("–í—Å–∏—á–∫–∏ –ø–æ–ª–µ—Ç–∞ —Å–∞ –∑–∞–¥—ä–ª–∂–∏—Ç–µ–ª–Ω–∏.");
    }

    if (rePass !== password) {
      return notify("–ü–∞—Ä–æ–ª–∏—Ç–µ —Ç—Ä—è–±–≤–∞ –¥–∞ —Å—ä–≤–ø–∞–¥–∞—Ç.");
    }

    await register(`${username}@123.123`, username, password);
    setToken(sessionStorage.getItem("authToken"));
    history.push("/");
  };
  return (
    <div className="register-form-wrapper">
      <form onSubmit={onSubmitHandler} className="register-form">
        <div>
          <i className="fas fa-user"></i>
          <input id="registerUsername" type="text" placeholder="–ø–æ—Ç—Ä–µ–±–∏—Ç–µ–ª—Å–∫–æ –∏–º–µ" name="username" />
        </div>
        <div>
          <i className="fas fa-key"></i>
          <input id="registerPassword" type="password" placeholder="–ø–∞—Ä–æ–ª–∞" name="password" />
        </div>
        <div>
          <i className="fas fa-key"></i>
          <input id="repeatPassword" type="password" placeholder="–ø–æ–≤—Ç–æ—Ä–∏ –ø–∞—Ä–æ–ª–∞" name="repass" />
        </div>
        <button className="createBtn">–†–ï–ì–ò–°–¢–†–ê–¶–ò–Ø</button>
        <p className="message">
          –ò–º–∞—Ç–µ —Ä–µ–≥–∏—Å—Ç–∞—Ü–∏—è?{" "}
          <Link to="/login" className="regToggle">
            –í–ª–µ–∑—Ç–µ –≤ –ø—Ä–æ—Ñ–∏–ª–∞ —Å–∏
          </Link>
        </p>
      </form>
    </div>
  );
};

export default Register;
